import { ReactNode } from "react"
import { FileRejection, useDropzone } from "react-dropzone"
import styled from "@emotion/styled"
import { Box, SxProps, useTheme } from "@rp/react/web/mui/core"

type Props = {
  children: React.ReactNode
  disabled?: boolean
  onDrop: (files: Array<File>) => void
  onDropRejected: (files: Array<FileRejection>) => void
  accept?: string
  multiple?: boolean
  Overlay: ReactNode
  sx?: SxProps
}

export const DROP_ZONE_AREA_CLASS_NAME = "drop-zone-area"

const DropArea = styled("div", {
  shouldForwardProp: prop => prop !== "isDragActive",
})(() => ({}))

export const DropZone = ({
  children,
  disabled,
  onDrop,
  onDropRejected,
  accept,
  multiple = false,
  Overlay,
  sx,
}: Props) => {
  const theme = useTheme()
  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    onDropRejected,
    noClick: true,
    noKeyboard: true,
    accept,
    disabled,
    multiple,
  })
  const styles = typeof sx === "function" ? sx({ theme }) : sx
  return (
    <Box sx={{ width: "100%", height: "100%", ...styles }}>
      <DropArea className={DROP_ZONE_AREA_CLASS_NAME} {...getRootProps({ isDragActive })}>
        {isDragActive && Overlay}
        <input data-testid="upload-input" {...getInputProps()} />
        {children}
      </DropArea>
    </Box>
  )
}
